import Card from "../../components/general/card"

const Letter = ({ title, body, signature, className }) => {

    return (
        <Card className={`${className} max-w-2xl`} innerClassName="px-5 py-4">
        {/* <section className={`${className} px-5 py-4 max-w-2xl bg-yellow-100 rounded-lg shadow-lg border-4 border-yellow-300`}> */}
            
            <div className="font-bold text-2xl italic">
                {title}
            </div>

            <p className="whitespace-pre-wrap mt-4 text-lg">
                {body.trim()}
            </p>

            <div className="whitespace-pre-wrap mt-4 text-gray-500 italic">
                {signature.trim()}
            </div>

        {/* </section> */}
        </Card>
    )
}

export default Letter

